html,
body {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: black;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: -webkit-linear-gradient(
    151deg,
    #e2d7dc,
    #c5d8d5,
    #aaeec7,
    #eabfd2,
    #d2c8ea,
    #e5eac8,
    #fbb5b5
  );
  background: -o-linear-gradient(
    151deg,
    #e2d7dc,
    #c5d8d5,
    #aaeec7,
    #eabfd2,
    #d2c8ea,
    #e5eac8,
    #fbb5b5
  );
  background: linear-gradient(
    299deg,
    #e2d7dc,
    #c5d8d5,
    #aaeec7,
    #eabfd2,
    #d2c8ea,
    #e5eac8,
    #fbb5b5
  );
  background-size: 600% 600%;
  -webkit-animation: Gradient 20s ease infinite;
  animation: Gradient 20s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

h1 {
  margin-top: 0;
}
a {
  color: blue;
  text-decoration: none;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
hr {
  width: 50%;
}
img {
  width: 3%;
}
.headerContainer {
  font-size: 1.3rem;
  letter-spacing: 3px;
  width: 60%;
  margin-left: 20%;
}
.textContent {
  margin-left: 10%;
  margin-right: 10%;
  font-size: 1.2rem;
  border-radius: 100px;
}
.projectInfo {
  padding-bottom: 3%;
}
iframe {
  margin-top: 1%;
  height: 600px;
  width: 810px;
}
.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.col1 {
  margin-right: 6%;
}
.col2,
.col3 {
  margin-left: 3%;
  margin-right: 3%;
}
.col4 {
  margin-left: 6%;
}



